<template>
  <el-dialog
      :title="_title"
      :visible="_visible"
      :width="width"
      :before-close="onCancel"
      center>
      <span>Are you sure want to delete?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onCancel">Cancel</el-button>
        <el-button type="primary" @click="onDelete">Yes</el-button>
      </span>
    </el-dialog>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "Delete Confirmation"
    },
    description: {
      type: String,
      default: "Are you sure want to delete?"
    },
    visible: {
      type: Boolean,
      required: true
    },
    onCancel: {
      type: Function,
      default: () => {
        this._visible = false;
      }
    },
    onDelete: {
      type: Function,
      required: true
    }
  },
  computed: {
    _title() {
      return this.title;
    },
    _description() {
      return this.description;
    },
    _visible() {
      return this.visible;
    }
  },
  data() {
    return {
      width: "30%",
      windowWidth: window.innerWidth
    };
  },
  methods: {
    makeDialogResponsive() {
      if (this.windowWidth < 990) {
        this.width = "90%";
      } else {
        this.width = "30%";
      }
    }
  },
  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },
  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  },
};
</script>



