<template>
  <el-table :data="transactions" v-loading="loading" element-loading-text="Memuat data..." border fit highlight-current-row style="width: 100%">
    <el-table-column prop="shop" label="Shop" sortable>
      <template slot-scope="scope">
        {{scope.row.shop.name}}
      </template>
    </el-table-column>

    <el-table-column prop="total_price" label="Total Price" sortable align="right" header-align="left">
      <template slot-scope="scope">
        {{ scope.row.total_price | toIdr }}
      </template>
    </el-table-column>

    <el-table-column prop="payment" label="Payment" sortable align="right" header-align="left">
      <template slot-scope="scope">
        {{ scope.row.payment | toIdr }}
      </template>
    </el-table-column>

    <el-table-column prop="change" label="Change" sortable align="right" header-align="left">
      <template slot-scope="scope">
        {{ scope.row.change | toIdr }}
      </template>
    </el-table-column>

    <el-table-column prop="created_by" label="Created By" sortable>
      <template slot-scope="scope">
        {{ scope.row.created_by.name }}
      </template>
    </el-table-column>

    <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
      <template slot-scope="scope">
        <el-button type="primary" size="mini" @click="onViewBtnClicked(scope.row.transaction_id)" icon="el-icon-view"></el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  props: {
    transactions: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    onViewBtnClicked: {
      type: Function,
      required: true,
    }
  }
};
</script>



