<template>
  <div :class="className" :style="{height:height,width:width}"></div>
</template>

<script>
import moment from "moment";
import echarts from "echarts";
require("echarts/theme/macarons"); // echarts theme
import { debounce } from "@/utils";
import { materialColor } from "@/utils/colors";

export default {
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "350px"
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Object
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.initChart();
    if (this.autoResize) {
      this.__resizeHanlder = debounce(() => {
        if (this.chart) {
          this.chart.resize();
        }
      }, 100);
      window.addEventListener("resize", this.__resizeHanlder);
    }

    // // 监听侧边栏的变化
    const sidebarElm = document.getElementsByClassName("sidebar-containerx")[0];
    sidebarElm.addEventListener("transitionend", this.__resizeHanlder);
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    if (this.autoResize) {
      window.removeEventListener("resize", this.__resizeHanlder);
    }

    const sidebarElm = document.getElementsByClassName("sidebar-containerx")[0];
    sidebarElm.removeEventListener("transitionend", this.__resizeHanlder);

    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    setOptions({ chartData, field }) {
      let _chartData = chartData;
      let _xAxisData = [];
      let _series = [];
      let _legend = [];

      const isNotUndefined = _chartData !== undefined;
      let isNotEmpty = false;
      try {
        isNotEmpty = Object.keys(_chartData).length > 0;
      } catch (e) {
        isNotEmpty = true;
      }

      if (isNotUndefined && isNotEmpty) {
        const shopNames = Object.keys(_chartData); // shop name
        const firstShopName = shopNames[0];

        // use date in first object as xAxis, others should be the same
        _xAxisData = _chartData[firstShopName].map(shop => {
          return moment(shop.date).format("DD MMM");
        });

        let i = 0;
        _series = shopNames.map(shopName => {
          const chartColors = Object.values(materialColor.A400);
          const animationEasing = ["cubicInOut", "quadraticOut"];
          const animationDuration = 2800;

          const _data = _chartData[shopName].map(data => {
            return data[field];
          });

          // Change color
          i++;
          if (i % chartColors.length === 0) {
            i = 0;
          }

          return {
            name: shopName,
            smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: chartColors[i],
                lineStyle: {
                  color: chartColors[i],
                  width: 2
                }
              }
            },
            data: _data,
            animationDuration: animationDuration,
            animationEasing: animationEasing[0]
          };
        });

        _legend = shopNames;
      }

      this.chart.setOption({
        xAxis: {
          data: _xAxisData,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        grid: {
          left: 10,
          right: 10,
          bottom: 20,
          top: 30,
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          }
        },
        legend: {
          data: _legend
        },
        series: _series
      });
    },
    initChart() {
      this.chart = echarts.init(this.$el, "macarons");
      this.setOptions(this.chartData);
    }
  }
};
</script>



