<template>
  <el-dialog
    :title="dialogName"
    :visible="_visible"
    :width="width"
    :before-close="closeDialog"
    center>

    <table>
      <tr>
        <td>Shop name</td>
        <td>:</td>
        <td v-if="transaction.shop">{{transaction.shop.name}}</td>
      </tr>
      <tr>
        <td>Total price</td>
        <td>:</td>
        <td>{{transaction.total_price | toIdr}}</td>
      </tr>
      <tr>
        <td>Payment</td>
        <td>:</td>
        <td>{{transaction.payment | toIdr}}</td>
      </tr>
      <tr>
        <td>Change</td>
        <td>:</td>
        <td>{{transaction.change | toIdr}}</td>
      </tr>
      <tr>
        <td>Created by</td>
        <td>:</td>
        <td v-if="transaction.created_by">{{transaction.created_by.name}}</td>
      </tr>
    </table>

    <el-table :data="transactionDetails" element-loading-text="Memuat data..." border fit highlight-current-row style="width: 100%">
      <el-table-column prop="name" label="Product Name" sortable>
      </el-table-column>

      <el-table-column prop="quantity" label="Qty" sortable>
      </el-table-column>

      <el-table-column prop="stock_price" label="Stock Price" sortable align="right" header-align="left">
        <template slot-scope="scope">
          {{ scope.row.stock_price | toIdr }}
        </template>
      </el-table-column>

      <el-table-column prop="price" label="Price" sortable align="right" header-align="left">
        <template slot-scope="scope">
          {{ scope.row.price | toIdr }}
        </template>
      </el-table-column>

      <el-table-column prop="discount_type_string" label="Discount Type" sortable>
      </el-table-column>

      <el-table-column prop="discount_percentage" label="Discount Percentage" sortable>
      </el-table-column>

      <el-table-column prop="discount_price" label="Discount Price" sortable align="right" header-align="left">
        <template slot-scope="scope">
          {{ scope.row.discount_price | toIdr }}
        </template>
      </el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean,
      required: true
    },
    transaction: {
      required: true
    },
    width: {
      type: String,
      default: "70%"
    }
  },
  computed: {
    _visible() {
      return this.visible;
    },
    transactionDetails() {
      if (this.transaction) {
        return this.transaction.details;
      }
      return [];
    },
    dialogName() {
      if (this.transaction.shop && this.transaction.transaction_id) {
        return `${this.transaction.shop.name} #${
          this.transaction.transaction_id
        }`;
      }
      return "";
    }
  },
  methods: {
    closeDialog() {
      this.$emit("closeDialog");
    }
  }
};
</script>



